import { getCurrentUserData } from '../services/service';


const defaultState = {
    info: null,
    isAuth: false
}

export const userReducer = (state: any = defaultState, action: any) => {
    console.log(state)
    console.log(action)

    switch (action.type) {
        case "GET_USER_DATA":
            return {...state, ...action.payload}
        default:
            return state
    }
}
